import React, { SyntheticEvent } from "react";
import RectButton from "../../atoms/rect-button/RectButton";
import PromotionSection from "../promotion-section/PromotionSection";
import ResumeSlider from "../resume-slider/Resume-Slider";
import './start-window.css';

interface resumeProps {
    id: string;
    template: string;
    preview: any;
    dir: string;
}

interface StartWindowProps {
    resumeList: resumeProps[];
    startCreaction: (id: string) => void;
}

export default class StratWindow extends React.Component <StartWindowProps, {id: string | null}> {
    constructor(props: StartWindowProps) {
        super(props);
        this.state = {id: null};
    }
    render() {
        return (
            <div className="start-section">
                <div>
                    <PromotionSection />
                    <RectButton className="start-button el-center" onClick={() => {this.state.id && this.props.startCreaction(this.state.id)}}>create my resume</RectButton>
                </div>
                <div onChange={(event: React.ChangeEvent<HTMLInputElement>) => {this.setState({id: (event.target as HTMLInputElement).value})}} className="slider-wrapper">
                    <ResumeSlider resumeList={this.props.resumeList} />
                </div>
        </div>
    )
    }
}
