import classNames from "classnames";
import React from "react";
import Footer from "../footer/Footer";
import PDFProps from '../PDFprops';
import TemplatePreview from "../template-preview/TemplatePreview";
import UserInfo from "../user-info/UserInfo";
import './creating-section.css';
const TEMPLATE_FOR_DEMO = `




<style>
* {
    margin: 0;
    padding: 0;
}
@import url('https://fonts.googleapis.com/css2?family=Abhaya+Libre:wght@600&display=swap');
.template__body {
    box-sizing: border-box;
    --main-text-color: #353e91;
    font-family: 'Abhaya Libre', serif;
    padding: 7em;
    background-color: #fff;
}
.template__name {
    text-align: center;
    font-size: 7.3em;
    color: var(--main-text-color);
}
.template__user-image {
    width: 10em;
    height: 10em;
    object-fit: contain;
}
.template__contacts-list {
    margin-top: 100px;
    display: flex;
    list-style: none;
    justify-content: space-between;
    font-size: 2em;
    color: #7e7e7e;
}
.template__contacts-list__item {
    min-width: 10em;
    display: flex;
    align-items: center;
}
.template__contacts-list__item a {
    text-decoration: underline;
    color: #000000;
}
.template__contact-list__item__icon {
    width: 1.5em;
    height: 1.5em;
    display: inline-block;
    border: solid #000000 1px;
    border-radius: 50%;
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.template__contact-list__item__icon svg {
    width: 1em;
    height: 1em;
    fill: var(--main-text-color);
}
.template__default-section-header {
    margin-top: 40px;
    font-size: 4.5em;
    color: var(--main-text-color);
}
.template__default-text {
    color: #7e7e7e;
    font-size: 2.5em;
}
.template__about-text {
    margin-top: 20px;
}
.template__work-experience-list {
    font-family: sans-serif;
    margin-top: 30px;
    list-style: none;
}
.template__work-experience__main-info-wrapper {
    display: flex;
    justify-content: space-between;
}
.template__work-experience-list__item {
    margin-top: 20px;
}
.template__work-experience__title {
    font-size: 3em;
}
.template__work-experience__company {
    font-size: 2.4em;
    color: #757575;
}
.template__work-experience__about {
    font-size: 1.6em;
    margin-top: 16px;
    color: #7e7e7e;
}
.template__work-experience__period {
    font-size: 1.2em;
    color: #ce7689;
}
.template__skills-list {
    margin-top: 35px;
    list-style: none;
    font-family: sans-serif;
}
.template__skills-list__item {
    width: 100%;
}
.template__skills-list__title {
    display: inline-block;
    font-size: 1.8em;
    width: 10em;
}
.template__skills-list__process {
    margin-left: 20px;
    display: inline-block;
    width: 20em;
    height: 1em;
    background-color: #7e7e7e;
}
.template__skills-list__process-bar {
    height: 100%;
    background-color: var(--main-text-color);
}
</style>
<div class="template__body">
<h1 class="template__name">{{name}}</h1>
{{#if img}}
<img src="{{img}}" class="template__user-image" />
{{/if}}
<ul class="template__contacts-list">
<li class="template__contacts-list__item"><div class="template__contact-list__item__icon"><svg fill="#353e91" xmlns="http://www.w3.org/2000/svg" id="Capa_1" enable-background="new 0 0 479.058 479.058" height="512" viewBox="0 0 479.058 479.058" width="512"><path d="m434.146 59.882h-389.234c-24.766 0-44.912 20.146-44.912 44.912v269.47c0 24.766 20.146 44.912 44.912 44.912h389.234c24.766 0 44.912-20.146 44.912-44.912v-269.47c0-24.766-20.146-44.912-44.912-44.912zm0 29.941c2.034 0 3.969.422 5.738 1.159l-200.355 173.649-200.356-173.649c1.769-.736 3.704-1.159 5.738-1.159zm0 299.411h-389.234c-8.26 0-14.971-6.71-14.971-14.971v-251.648l199.778 173.141c2.822 2.441 6.316 3.655 9.81 3.655s6.988-1.213 9.81-3.655l199.778-173.141v251.649c-.001 8.26-6.711 14.97-14.971 14.97z"/></svg>
</div> {{email}}</li>
<li class="template__contacts-list__item"><div class="template__contact-list__item__icon"><svg fill="#353e91" xmlns="http://www.w3.org/2000/svg" viewBox="-95 0 511 511.6402"><path d="m315.710938 292.25-288.894532-288.792969c-10.582031-8.402343-26.496094-.382812-26.496094 12.183594v394.667969c0 8.832031 7.167969 16 16 16 4.160157 0 8.148438-1.601563 10.433594-3.90625l80.039063-69.738282 65.28125 152.511719c1.109375 2.601563 3.199219 4.652344 5.824219 5.71875 1.28125.488281 2.625.746094 3.96875.746094 1.429687 0 2.859374-.300781 4.203124-.875l68.691407-29.441406c5.417969-2.300781 7.9375-8.574219 5.613281-13.992188l-63.191406-147.691406h107.136718c8.832032 0 16-7.167969 16-16 0-2.582031-.660156-6.464844-4.609374-11.390625zm0 0"/></svg>
</div> <a href="{{links.linkedIn}}">{{links.linkedInLink}}</a></li>
<li class="template__contacts-list__item"><div class="template__contact-list__item__icon"><svg fill="#353e91" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" id="Capa_1" x="0px" y="0px" viewBox="0 0 513.64 513.64" style="enable-background:new 0 0 513.64 513.64;" xml:space="preserve">
        <g>
            <path d="M499.66,376.96l-71.68-71.68c-25.6-25.6-69.12-15.359-79.36,17.92c-7.68,23.041-33.28,35.841-56.32,30.72    c-51.2-12.8-120.32-79.36-133.12-133.12c-7.68-23.041,7.68-48.641,30.72-56.32c33.28-10.24,43.52-53.76,17.92-79.36l-71.68-71.68    c-20.48-17.92-51.2-17.92-69.12,0l-48.64,48.64c-48.64,51.2,5.12,186.88,125.44,307.2c120.32,120.32,256,176.641,307.2,125.44    l48.64-48.64C517.581,425.6,517.581,394.88,499.66,376.96z"/>
        </g>
    </svg>
</div> {{phone}}</li>
</ul>
<h2 class="template__default-section-header">Profile</h2>
<div class="template__default-text template__about-text">{{{about}}}</div>
<h2 class="template__default-section-header">Work Experience</h2>

<ul class="template__work-experience-list">
{{#each experience}}
<li class="template__work-epxerience-list__item">
    <div class="template__work-experience__main-info-wrapper">
        <div class="template__work-experience__title-wrapper">
            <h4 class="template__work-experience__title">{{this.role}}</h4>
            <p class="template__work-experience__company">{{this.companyName}}</p>
        </div>
        <div class="template__work-experience__period-wrapper">
            <p class="template__work-experience__period">{{this.startYear}} - {{this.endYear}}</p>
        </div>
    </div>
    <div class="template__work-experience__about">{{{this.description}}}</div>
</li>
{{/each}}
</ul>
<h2 class="template__default-section-header">Skills</h2>
<ul class="template__skills-list">
{{#each skills}}
    <li class="template__skills-list__item">
        <span class="template__skills-list__title">{{this.skillName}}</span>
        <div class="template__skills-list__process">
            <div style="width: {{this.level}}%" class="template__skills-list__process-bar"></div>
        </div>
    </li>
{{/each}}
</ul>

</div>
`
const {URL} = require('../../../API.json');

interface CreatingSectionProps {
    templateId: string;
}


interface CreatingSectionState {
    userData: PDFProps;
    template: string;
    showTemplate: boolean;
}


export default class CreatingSection extends React.Component<CreatingSectionProps, CreatingSectionState> {
    constructor(props: CreatingSectionProps) {
        super(props);
        this.state = {userData: 
            {
                name:      undefined,
                img:       undefined,
                phone:     undefined,
                email:     undefined,
                skype:     undefined,
                telegram:  undefined,
                country:   undefined,
                city:      undefined,
                links: {
                    linkedInLink:  undefined,
                    gitHubLink:    undefined,
                    portfolioLink: undefined,
                    customLinks:   []
                },
                about: '',
                experience: {},
                skills: {}
            },
            template: '',
            showTemplate: false
        };
        this.setUserData = this.setUserData.bind(this);
        this.showTemplate = this.showTemplate.bind(this);
        this.hideTemplate = this.hideTemplate.bind(this);
    }
    componentDidMount() {
        // fetch(URL + '/0/0.hbs').then(data => data.text()).then(data => this.setState({template: data}));
        this.setState({template: TEMPLATE_FOR_DEMO})
    }
    setUserData(userData: PDFProps) {
        this.setState({userData});
    }
    showTemplate() {
        this.setState({showTemplate: true});
    }
    hideTemplate() {
        this.setState({showTemplate: false})
    }
    render() {
        return <><section className="creating-section">
            <button onClick={this.showTemplate} className="show-template-button"></button>
            <UserInfo setUserData={this.setUserData} userData={this.state.userData} />
            <TemplatePreview hide={this.hideTemplate} className={classNames(this.state.showTemplate && "shown")} id={this.props.templateId} userData={this.state.userData} template={this.state.template} />
        </section>
        <Footer />
        </>
    }
}