import React from 'react';
import './slider-element.css';

type SliderElementProps = any;
export default class SliderElement extends React.Component <SliderElementProps> {
    constructor(props: SliderElementProps) {
        super(props);
    }
    render() {
        const {src, alt, key, styles, templateID} = this.props;
        return(
            <div data-id={templateID} style={styles} key={key} className="slider-item">
                <img data-id={templateID} src={src} alt={alt} />
            </div>
        )
    }
}