import React from "react";
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import RectButton from "../../atoms/rect-button/RectButton";
import './about-modal-content.css';


interface AboutModalState {
    text: string;
}
export default class AboutModalContent extends React.Component <any, AboutModalState> {
    constructor(props: any) {
        super(props);
        this.state = {text: props.content};
        this.handleChange = this.handleChange.bind(this);
    }
    handleChange(value: string) {
        this.setState({text: value})
    }
    render() {
        console.log(this.props);
        return <div className="modal-content">
            <h2 className="info-block-header bold t-center">about me</h2>
            <ReactQuill value={this.state.text}
                  onChange={this.handleChange} className="text-field" />
            <div className="buttons-container">
                <RectButton onClick={() => {this.props.setAbout(this.state.text); this.props.closeModal()}}>Save changes</RectButton><RectButton className="gray-button" onClick={this.props.closeModal}>cancel</RectButton>
            </div>
        </div>
    }
}