import CommonProps from '../../common-props';
import classNames from 'classnames';
import './bordered-button.css';
import React from 'react';
interface ElemProps extends CommonProps {
    onClick?: (event: React.SyntheticEvent) => void;
}

export default function BorderedButton(props: ElemProps) {
    const {className, style, onClick} = props;
    return <button onClick={onClick} style={{...style}} className={classNames("bordered-button medium bold", className)}>{props.children}</button>
}