import classNames from 'classnames';
import React from 'react';
import './skill-settings.css';
import CommonProps from '../../common-props';
import ControlsButtons from '../controls-buttons/ControlsButtons';
import GroupWrapper from '../group-wrapper/GroupWrapper';
import TitledInputWrapper from '../titled-input/TitledInput';
import TextInput from '../text-input/TextInput';
import Range from '../range-input/RangeInput';
interface ElemProps extends CommonProps {
    id: string;
    skillName: string;
    yearsOfExperience: number | undefined;
    level: number;
    change: (id: string, options: {yearsOfExperience?: number | undefined, level?: number}) => void;
    remove: (id: string) => void;
    moveTop: (id: string) => void;
}

const SKILL_LEVELS:{name: string, color: string}[] = [
    {
        name: 'none',
        color: '2f2f2f',
    },
    {
        name: 'notice',
        color: 'ec1010'
    },
    {
        name: 'medium',
        color: 'da6f0b'
    },
    {
        name: 'good',
        color: 'adc309'
    },
    {
        name: 'high',
        color: '9cda0b'
    },
    {
        name: 'enhanced',
        color: '28c309'
    }

]
export default class SkillSettings extends React.Component <ElemProps, {level: number}> {
    constructor(props: ElemProps) {
        super(props);
        const {yearsOfExperience, level} = this.props;
        this.state = {level}
        this.onLevelInput = this.onLevelInput.bind(this);
        this.onYearInput = this.onYearInput.bind(this);
    }
    onYearInput(event: React.SyntheticEvent) {
        const target = event.target as HTMLInputElement;
        const value = +target.value;
        
        this.props.change(this.props.id, {yearsOfExperience: value});
    }
    onLevelInput(event: React.SyntheticEvent) {
        const target = event.target as HTMLInputElement;
        const value = +target.value;
        // const level = value > 0 ? value : 1;
        
        // this.setState({level});
        this.props.change(this.props.id, {level: value});
    }
    render() {
        
        const {id, skillName, yearsOfExperience, level, remove, moveTop} = this.props;
        const skillLevel = SKILL_LEVELS[level];
        return <GroupWrapper className="skill-settings-container">
            <>
                <div className="controls">
                    <ControlsButtons.ShowButton onClick={() => moveTop(id)}/>
                    <ControlsButtons.DeleteButton onClick={() => remove(id)} />
                </div>
                <h2 className="info-block-header bold">{skillName}</h2>
                <div className="skill-settings-inputs-wrapper">
                    {/* <div onInput={this.onYearInput}>
                    </div> */}
                        <TitledInputWrapper title="Years of expeience" protectedSign={false}>
                            <TextInput name="unused" onInput={this.onYearInput} placeholder="years"/>
                        </TitledInputWrapper>
                    <TitledInputWrapper title={<>Level — <span style={{color: '#' + skillLevel.color}}>{skillLevel.name}</span></>} protectedSign={false}>
                        <div style={{
                            '--skill-color': '#' + skillLevel.color,
                            '--skill-bg-color': '#' + skillLevel.color + '99'
                            } as React.CSSProperties} className="level-input-wrapper">
                            <div className="input-mark-container">
                                <span className="level-input-mark"></span>
                                <span className="level-input-mark"></span>
                                <span className="level-input-mark"></span>
                                <span className="level-input-mark"></span>
                            </div>
                            <Range onInput={this.onLevelInput} name='unused' min={0} max={5} value={this.state.level} />
                        </div>
                    </TitledInputWrapper>
                </div>
            </>
        </GroupWrapper>
    }
}