import React from "react";
import HtmlDisplay from "../../atoms/html-display/HtmlDisplay";
import PDFProps from "../PDFprops";
import Handlebars from "handlebars";
import RectButton from "../../atoms/rect-button/RectButton";
import {savePDF} from '@progress/kendo-react-pdf';
import classNames from "classnames";
import './template-preview.css';
const {URL} = require('../../../API.json');
interface TemplateProps {
    id: string;
    userData: PDFProps;
    template: string;
    className?: string;
    hide?: () => void;
}
interface TemplateState {
    wrapperWidth: number;
}
interface TemplateData {
    name:      string | undefined,
    img:       string | undefined | ArrayBuffer,
    phone:     string | undefined,
    email:     string | undefined,
    skype:     string | undefined,
    telegram:  string | undefined,
    country:   string | undefined,
    city:      string | undefined,
    links: {
        linkedInLink:  string | undefined,
        gitHubLink:    string | undefined,
        portfolioLink: string | undefined,
        customLinks:   string[]
    },
    about: string,
    experience: {
        [key: string]: {
            role:        string | undefined,
            companyName: string | undefined,
            startMonth:  string | undefined,
            startYear:   string | undefined,
            endMonth:    string | undefined,
            endYear:     string | undefined,
            description: string | undefined,
            id?:         string | undefined
        }
    }
    skills: 
        {
            id:                string,
            skillName:         string,
            yearsOfExperience: number | undefined,
            level:             number,
            place: number
        }[]
}
export default class TemplatePreview extends React.Component <TemplateProps, TemplateState> {
    resumeContent: any;
    wrapperRef: React.RefObject<HTMLDivElement>;
    constructor(props: TemplateProps) {
        super(props);
        this.wrapperRef = React.createRef();
        this.resumeContent = React.createRef();
        this.state = {wrapperWidth: 0};
        this.resize = this.resize.bind(this);
    }
    componentDidMount() {
        window.addEventListener('resize', this.resize);
        this.resize();
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.resize);
    }
    resize() {
        this.setState({wrapperWidth: this.wrapperRef.current?.clientWidth || 0});
    }
    render() {
        const A4aspect = 297/210;
        const whitespace:HTMLDivElement | undefined | null = this.wrapperRef.current?.querySelector('.template-whitespace'); 
        const data:TemplateData = {...this.props.userData, ...{skills: Object.entries(this.props.userData.skills).map(entry => {const level = entry[1].level;
            return Object.assign(JSON.parse(JSON.stringify(entry[1])), {level: level /5 * 100})}).sort((a, b) => a.place - b.place)}};
        return (
            <div ref={this.wrapperRef} style={{height: this.state.wrapperWidth*A4aspect}} className={classNames(this.props.className, "preview-wrapper")}>
                <button onClick={this.props.hide} className="close-template-button"></button>
                <div style={{height: whitespace ? (whitespace?.clientWidth * A4aspect) : 0}} className="template-whitespace">
                        
                    <HtmlDisplay style={{fontSize: this.state.wrapperWidth/100 + 'px'}} ref={this.resumeContent}>
                        {Handlebars.compile(this.props.template)(data)}
                    </HtmlDisplay>
                    <RectButton className="download-btn" onClick={() => {
                        // fetch(URL + '/convert', {method: 'POST', body: JSON.stringify({templateId: this.props.id, templateProps: this.props.userData})}).then(data => data.arrayBuffer()).then(
                        //     resultBytes => {
                        //         var bytes = new Uint8Array(resultBytes); // pass your byte response to this constructor
        
                        //         var blob=new Blob([bytes], {type: "application/pdf"});// change resultByte to bytes
                                
                        //         var link=document.createElement('a');
                        //         link.href=window.URL.createObjectURL(blob);
                        //         link.download="your resume.pdf";
                        //         link.click();

                        //     }
                        // )
                        savePDF(this.resumeContent.current, {paperSize: 'A4'});

                    }}>download resume</RectButton>
                </div>
            </div>
        )
    }
}