import CommonProps from '../../common-props';
import classNames from 'classnames';
import './group-wrapper.css';
interface ElemProps extends CommonProps {}

export default function GroupWrapper(props:ElemProps) {
    const {className, style} = props;
    return (<div style={{...style}} className={classNames("group-wrapper", className)}>
        {props.children}
    </div>)
}