import React from "react";
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import SliderElement from "../../atoms/resume-preview/SliderElement";
import previewImg from '../../../media/JOBITT - Web site 2021-07-30 18-46-02 1.png';
interface resumeProps {
    id: string;
    template: string;
    preview: any;
    dir: string;
}
interface SliderProps {
    resumeList: resumeProps[];
}
interface SliderState {
    sliderIndex: number;
    sliderItemWidth: number;
    fullSliderItemWidth: number;
    screenWidth: number;
}

interface ControlsButtonsProps {
    className?: string;
    style?: React.CSSProperties;
    onClick?: (event: React.SyntheticEvent) => void;
    sliderXTransform?: number;
}

const NextArrow = function(props: ControlsButtonsProps) {
    const className = props.className + ' controls-btn next-btn';
    const {style, onClick, sliderXTransform} = props;
  return (
    <button
      className={className}
      style={{ ...style}}
      onClick={onClick}
    />
  );
}

const PreviousArrow = function(props: ControlsButtonsProps) {
    const className = props.className + ' controls-btn previous-btn';
    const {style, onClick } = props;
  return (
    <button
      className={className}
      style={{ ...style}}
      onClick={onClick}
    />
  );
}

const SLIDER_PROPS = {
    dots: false,
    infinite: false,
    slidesToShow: 5,
    slidesToScroll: 4,
    initialSlide: 0,
    arrows: false,
    responsive: [
        {
            breakpoint: 1500,
            settings: {
                slidesToShow: 4,
                slidesToScroll: 3
            }
        },
        {
            breakpoint: 960,
            settings:
            {
                slidesToShow: 2,
                slidesToScroll: 2
            }
        },
        {
            breakpoint: 600,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1
            }
        }
    ]
}

export default class ResumeSlider extends React.Component <SliderProps, SliderState> {
    sliderRef: React.RefObject<HTMLDivElement>;
    inited: boolean;
    slickRef: any;
    constructor(props: SliderProps) {
        super(props);
        this.state = {sliderIndex: 0, sliderItemWidth: 0, screenWidth: this.getScreenWidth(), fullSliderItemWidth: 0};
        this.sliderRef = React.createRef();
        this.inited = false;

        this.resizing = this.resizing.bind(this);
        this.setIndex = this.setIndex.bind(this);
    }
    setIndex(index: number) {
        this.setState({sliderIndex: index});
    }
    getScreenWidth(): number {
        const screenWidth: number = window.innerWidth;

        return screenWidth;
    }
    getSliderItemWidth(selector: string): number {
        const sliderItemHTMLNode = this.sliderRef && this.sliderRef.current?.querySelector(selector);
        if (sliderItemHTMLNode) return sliderItemHTMLNode.clientWidth;
        return 0;
    }
    resizing() {
        const screenWidth = this.getScreenWidth();
        const sliderItemWidth = this.getSliderItemWidth('.slider-item');
        const fullSliderItemWidth = this.getSliderItemWidth('.slick-slide');
        console.log(sliderItemWidth);
        this.setState({screenWidth, sliderItemWidth, fullSliderItemWidth});
        console.table({screenWidth, sliderItemWidth, fullSliderItemWidth});
    }
    componentDidMount() {
        window.addEventListener('resize', this.resizing);
        this.resizing();
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.resizing);
    }
    getBreakpointSettings(props: typeof SLIDER_PROPS) : {slidesToShow: number; slidesToScroll: number} {
        const width = this.state.screenWidth;
        const settings = props.responsive.sort((a, b) => a.breakpoint - b.breakpoint).find(el => {
            const {breakpoint, settings} = el;
            if (width < breakpoint) return settings;
        });
        return (settings?.settings || {slidesToShow: props.slidesToShow, slidesToScroll: props.slidesToScroll})

    }
    render() {
        const index = this.state.sliderIndex;
        const {slidesToShow, slidesToScroll} = this.getBreakpointSettings(SLIDER_PROPS);
        const {resumeList} = this.props;
        const sliderXTranform = this.state.screenWidth > 960 ? this.state.sliderItemWidth : 0;
        const fullSliderItemWidth = this.state.screenWidth > 960 ? this.state.fullSliderItemWidth : 0;
        const parentElement: HTMLElement | null | undefined = this.sliderRef && this.sliderRef.current?.parentElement;
        const parentWidth: number | undefined = parentElement?.clientWidth;
        const sliderContainerWidth = parentWidth ? parentWidth * 0.7 : 0;
        const {URL} = require('../../../API.json');
        const sliderProps = Object.assign(
            SLIDER_PROPS,
            {
                afterChange: this.setIndex,
                onReInit: () => {
                    const sliderItemWidth = this.getSliderItemWidth('.slider-item');
                    if (!this.inited && sliderItemWidth) {
                        this.inited = true;
                        this.resizing();
                    }
                }
            }
        )
        
        return (
            <>
            <p className="resume-count-text medium-fron t-center">{index + slidesToShow - 1} of {resumeList.length}</p>
            <div className="slider">
                
            <PreviousArrow onClick={() => index > 0 && this.slickRef.slickPrev()} />
            <div ref={this.sliderRef} className="resume-slider">
                <div className="full-slider" style={{width: `${sliderContainerWidth + fullSliderItemWidth}px`}}>
                    <Slider ref={(sliderRef: any) => this.slickRef = sliderRef} {...sliderProps} >
                        {resumeList.map((resumeEl: resumeProps, i: number) => {
                            const isActive: boolean = i > index + slidesToScroll - 1;
                            const id = 'preview_' + i;
                            return (
                                <div key={id}>
                                    <input disabled={isActive} style={{display: 'none'}} type="radio" name="slider" value={resumeEl.id} id={id} />
                                    <label className="slider-element-label" htmlFor={id}>
                                        <SliderElement styles={{opacity: isActive ? 0.7 : 1}} templateID={resumeEl.id} src={previewImg} />
                                    </label>
                                </div>
                            )
                            })}
                        {this.state.screenWidth > 960 && <div />}
                    </Slider>
                </div>
            </div>
            <NextArrow onClick={() => index + slidesToShow < resumeList.length && this.slickRef.slickNext()}/>
            </div>
            </>
        )
    }
}