import React from "react";
import HtmlDisplay from "../html-display/HtmlDisplay";
import Buttons from '../controls-buttons/ControlsButtons';
import Line from "../line/Line";
console.log(Buttons);

interface ExperienceItemProps {
    experienceData: {
        role:        string | undefined,
        companyName: string | undefined,
        startMonth:  string | undefined,
        startYear:   string | undefined,
        endMonth:    string | undefined,
        endYear:     string | undefined,
        description: string | undefined,
        id?:         string | undefined
    },
    remove: (id: string | undefined) => void;
    edit: (id: string | undefined) => void;
}
export default class ExperienceItem extends React.Component<ExperienceItemProps> {
    constructor(props: ExperienceItemProps) {
        super(props);
    }
    render() {
        const data = this.props.experienceData;
        const {remove, edit} = this.props;
        return (
        <div className="user-experience-list-item medium-font">
            <div className="controls">
                <Buttons.EditButton onClick={() => edit(data.id)} />
                <Buttons.DeleteButton onClick={() => remove(data.id)} />
            </div>
            <h4 className="experience-name bold upper">{data.role}</h4>
            <p className="company-name accent upper">{data.companyName}</p>
            <p className="experience-time">{data.startYear} - {data.endYear}</p>
            <div className="experience-description">
                <HtmlDisplay>{data.description}</HtmlDisplay>
            </div>
            <Line />
        </div>)
    }
}