import React from "react";
import CommonProps from '../../common-props';
import classNames from 'classnames';
import './skills-list.css';

interface SkillsListProps extends CommonProps {
    skills: string[];
}
export default class SkillsList extends React.Component<SkillsListProps> {
    constructor(props: SkillsListProps) {
        super(props);
    }
    render() {
        const {style, className} = this.props;
        return (
            <ul style={{...style}} className={classNames("skills-list", className)}>
                {this.props.skills.map((skill, i) => <li key={i + skill} className="skills-list-item">{skill}</li>)}
            </ul>
        )
    }
}