import React from 'react';
import CreatingSection from './components/sections/creating-section/CreatingSection';
import FinishCreatingSection from './components/sections/finish-creating-section/FinishCreatingSection';
import Header from "./components/sections/header/Header";
import StratWindow from './components/sections/start-window/StartWindow';

const API_IP = 'http://127.0.0.1:8080';

interface resumeProps {
  id: string;
  template: string;
  preview: any;
  dir: string;
}

interface AppState {
  id: string;
  stage: string;
  resumeList: resumeProps[];
  inited: boolean;
  initSuccess: boolean;
}

class App extends React.Component <any, AppState> {
  constructor(props: any) {
    super(props);
    this.state = {
      id: 't_0',
      stage: 'start',
      resumeList: [
        {
          template: '0.hbs',
          preview: 'JOBITT - Web site 2021-07-30 18-46-02 1.png',
          dir: '0',
          id: 't_0'
        },
        {
          template: '0.hbs',
          preview: 'JOBITT - Web site 2021-07-30 18-46-02 1.png',
          dir: '0 — копия',
          id: 't_1'
        },
        {
          template: '0.hbs',
          preview: 'JOBITT - Web site 2021-07-30 18-46-02 1.png',
          dir: '0 — копия — копия',
          id: 't_2'
        },
        {
          template: '0.hbs',
          preview: 'JOBITT - Web site 2021-07-30 18-46-02 1.png',
          dir: '0 — копия — копия — копия',
          id: 't_3'
        },
        {
          template: '0.hbs',
          preview: 'JOBITT - Web site 2021-07-30 18-46-02 1.png',
          dir: '0 — копия — копия — копия (2)',
          id: 't_4'
        },
        {
          template: '0.hbs',
          preview: 'JOBITT - Web site 2021-07-30 18-46-02 1.png',
          dir: '0 — копия — копия — копия — копия',
          id: 't_5'
        }
      ],
      inited: true,
      initSuccess: true
    };
    this.startCreation = this.startCreation.bind(this);
    this.initTemplates = this.initTemplates.bind(this);
  }
  componentDidMount() {
    const url: string = API_IP + '/init';
    console.log(url);
    // fetch(url).then(this.initTemplates);
    this.setState({});

  }
  async initTemplates(responce: any) {
    if (!responce.ok) {
      return this.setState({initSuccess: false});
    }
    const data = await responce.json();
    this.setState({resumeList: data, inited: true, initSuccess: true});
  }
  startCreation(id: string) : void {
    this.setState({stage: 'creating', id});
  }
  render () {
    const {stage, id} = this.state;
    switch(stage) {
      case 'start':
        return (
          <>
    
            <Header />
            <main>
              <StratWindow startCreaction={this.startCreation} resumeList={this.state.resumeList} />
            </main>
          </>
        );
      case 'creating' :
        return (
          <>

            <Header />
            <main>
              <CreatingSection templateId={id} />
            </main>
          </>
        )
      case 'finish' :
          return (
            <>
              <Header />
              <FinishCreatingSection />
            </>
          )
      
    }
  }
}


export default App;

