import CommonProps from '../../common-props';
import classNames from 'classnames';
import './controls-button.css';
import React from 'react';
import deleteIcon from '../../../media/svg/Delete.svg';
import editIcon from '../../../media/svg/Edit.svg';
import showIcon from '../../../media/svg/arrow.svg';
interface ElemProps extends CommonProps {
    onClick?: (event: React.SyntheticEvent) => void;
}

const ControlsButton = function(props: ElemProps) {
    const {className, style, onClick} = props;
    return <button onClick={onClick} style={{...style}} className={classNames("controls-button", className)}>{props.children}</button>
}
export default {
    DeleteButton: (props: ElemProps) => {
        const {className, style, onClick} = props;
        return <ControlsButton onClick={onClick} style={{...style}} className={classNames(className)}><img src={deleteIcon} alt=""/></ControlsButton>
    },
    EditButton: (props: ElemProps) => {
        const {className, style, onClick} = props;
        return <ControlsButton onClick={onClick} style={{...style}} className={classNames(className)}><img src={editIcon} alt=""/></ControlsButton>
    },
    ShowButton: (props: ElemProps) => {
        const {className, style, onClick} = props;
        return <ControlsButton onClick={onClick} style={{...style}} className={classNames(className)}><img src={showIcon} alt=""/></ControlsButton>
    }
}