import React from "react";
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import RectButton from "../../atoms/rect-button/RectButton";
import SelectInput from "../../atoms/select-input/SelectInput";
import TextInput from "../../atoms/text-input/TextInput";
import TitledInputWrapper from "../../atoms/titled-input/TitledInput";
import './experience-modal-content.css';

interface UserExperienceData {
    role:        string | undefined,
    companyName: string | undefined,
    startMonth:  string | undefined,
    startYear:   string | undefined,
    endMonth:    string | undefined,
    endYear:     string | undefined,
    description: string | undefined,
    id?:         string | undefined
}

interface ExperienceModalProps {
    userExperience: UserExperienceData | undefined;
    modalId: string | null;
    setExperience: (userData: UserExperienceData) => void;
    closeModal?: () => void;
}
interface ExperienceModalState {
    userData: UserExperienceData | undefined;
}
export default class ExperienceModalContent extends React.Component <ExperienceModalProps, ExperienceModalState> {
    constructor(props: ExperienceModalProps) {
        super(props);
        this.state = {userData: Object.assign(
            {
                role:        undefined,
                companyName: undefined,
                startMonth:  undefined,
                startYear:   undefined,
                endMonth:    undefined,
                endYear:     undefined,
                description: '',
                id:         this.props.modalId
            },
            props.userExperience
            )};
        this.handleChange = this.handleChange.bind(this);
        this.getInputsData = this.getInputsData.bind(this);
    }
    handleChange(value: string) {
        this.setState(prevState => ({
            ...prevState,
            userData: Object.assign(
                prevState.userData,
                {description: value}
            )
        }))
    }
    getInputsData(event: React.SyntheticEvent) {
        const target: HTMLInputElement | HTMLSelectElement = event.target as HTMLInputElement | HTMLSelectElement;
        const {name, value} = target;
        if (name)
        this.setState(prevState => ({
            ...prevState,
            userData: Object.assign(
                prevState.userData,
                {[name]: value}
            )
        }));
    }
    render() {
        const userData = this.state.userData;
        if (userData) return (
        <div onChange={this.getInputsData} className="modal-content">
            <h2 className="info-block-header bold t-center">add experience</h2>
            <TitledInputWrapper className="experience-input-wrapper" title="Role" protectedSign={false}>
                <TextInput value={userData.role} name="role" placeholder="Role" />
            </TitledInputWrapper>
            <TitledInputWrapper className="experience-input-wrapper" title="Company name" protectedSign={false}>
                <TextInput value={userData.companyName} name="companyName" placeholder="Company name" />
            </TitledInputWrapper>
            <div className="start-date-inputs date-inputs-wrapper">
                <TitledInputWrapper className="month-input-wrapper" title="Start month" protectedSign={false}>
                    <SelectInput defaultValue={userData.startMonth} name="startMonth" values={[{title: 'May', value: 'May'}]} />
                </TitledInputWrapper>
                <TitledInputWrapper className="start-year-input-wrapper" title="Start year" protectedSign={false}>
                    <TextInput className="t-center" value={userData.startYear} name="startYear" placeholder="year" />
                </TitledInputWrapper>
            </div>
            <div className="end-date-inputs date-inputs-wrapper">
                <TitledInputWrapper className="month-input-wrapper" title="End month" protectedSign={false}>
                    <SelectInput defaultValue={userData.endMonth} name="endMonth" values={[{title: 'May', value: 'May'}]} />
                </TitledInputWrapper>
                <TitledInputWrapper className="start-year-input-wrapper" title="End year" protectedSign={false}>
                    <TextInput className="t-center" value={userData.endYear} name="endYear" placeholder="year" />
                </TitledInputWrapper>
            </div>
            <ReactQuill value={userData.description}
                  onChange={this.handleChange} className="text-field" />
            <div className="buttons-container">
                <RectButton onClick={() => {this.props.setExperience(userData); this.props.closeModal && this.props.closeModal()}}>Save changes</RectButton><RectButton className="gray-button" onClick={this.props.closeModal}>cancel</RectButton>
            </div>
        </div>)
    }
}