import CommonProps from '../../common-props';
import classNames from 'classnames';
import './titled-input-wrapper.css';
interface ElemProps extends CommonProps {
    title: string | React.ReactElement, protectedSign: boolean;
}
export default function TitledInputWrapper(props: ElemProps) {
    const {title, protectedSign, className, style} = props;
    return <label style={{...style}} className={classNames("titled-input-label", className, protectedSign && "protected")}>
        <p className="titled-input-title medium-font">{title}</p>
        {props.children}
    </label>
}