import CommonProps from '../../common-props';
import classNames from 'classnames';
import './progress-bar.css';
interface ElemProps extends CommonProps {
    progress: number
}
export default function ProgressBar(props: ElemProps) {
    const {progress, className, style} = props;
    return <div style={{...style}} className={classNames("progress-bar", className)}>
        <div style={{width: progress + '%'}} className="progress-line"></div>
    </div>
}