import CommonProps from '../../common-props';
import classNames from 'classnames';
import GroupWrapper from '../group-wrapper/GroupWrapper';
import './file-input.css';
import React from 'react';
import RectButton from '../rect-button/RectButton';
interface ElemProps extends CommonProps {
    onFile?: (file: File) => void;
    name?: string;
}
interface ElemState {
    assigned: boolean;
    error: 'type' | 'size' | null;
    fileName: string | null;
}
export default class FileInput extends React.Component <ElemProps, ElemState> {
    inputRef: React.RefObject<HTMLInputElement>;
    constructor(props: ElemProps) {
        super(props);
        this.state = {assigned: false, error: null, fileName: null};
        this.inputRef = React.createRef();
        this.onChange = this.onChange.bind(this);
    }
    onChange(event: React.SyntheticEvent) {
        const target = event.target as HTMLInputElement;
        if (!target.files) return;
        const file = target.files[0];
        if (!file.type.startsWith('image/')) return this.setState({fileName: file.name, assigned: false, error: 'type'});
        if (file.size > 1024 * 1024 * 5) return this.setState({fileName: file.name, assigned: false, error: 'size'});
        this.setState({assigned: true, fileName: file.name});
        if (file && this.props.onFile) this.props.onFile(file);
    }
    render() {
        const {name, style, className} = this.props;
        const {assigned, fileName} = this.state;
        return (
            <GroupWrapper style={{...style}} className={classNames(className, "file-input-wrapper")}>
                <label className="file-img-input-label" htmlFor="user-image-input">
                    <input ref={this.inputRef} onChange={this.onChange} className="file-input" id="user-image-input" type="file" name={name} accept="image/jpeg,image/png,image/gif" />
                    <p className="file-info t-center b-600">Max file size; 5mb, {assigned ? 'assigned' : fileName ? 'rejected' : ''} {fileName}</p>
                    <RectButton onClick={() => this.inputRef.current?.click()} className="el-center file-choose-button">choose image</RectButton>
                </label>
            </GroupWrapper>
        )
    }
}