import CommonProps from '../../common-props';
import classNames from 'classnames';
import './select-input.css';
interface ElemProps extends CommonProps {
    name: string; values: {title:string, value:string}[]; defaultValue?: string;
}
export default function SelectInput(props:ElemProps) {
    const {className, style, name, defaultValue} = props;
    return (
        <label className="select-element-label">
            <select name={name} defaultValue={defaultValue} style={{...style}} className={classNames("select-element", className)}>
                {props.values.map((option, i) => <option key={props.name + '_' + i} value={option.value}>{option.title}</option>)}
            </select>
        </label>
    )
}