import React from 'react';
import './range.css';

class Range extends React.Component<any, any> {
    inputRef: React.RefObject<HTMLInputElement>
    constructor(props: any) {
        super(props);
        this.state = {value: this.props.value};
        this.onInput = this.onInput.bind(this);
        this.inputRef = React.createRef();
        this.calculateGradient = this.calculateGradient.bind(this);
    }
    onInput(event: React.SyntheticEvent) {
        this.props.onInput(event);
        this.calculateGradient();
    }
    componentDidMount() {
        this.calculateGradient();
    }
    calculateGradient() {
        const input = this.inputRef.current as any;
        const {value, max, min} = input;
        const valuesRange = Math.abs(max - min);
        const activeLineWidthPercent = (value - min) / valuesRange;
        this.setState({activeLineWidthPercent, value});
        input.style.setProperty('--active-width', activeLineWidthPercent * 100 + '%');
    }
    render() {
        const defaults = {
            step: 1,
            min: 0,
            max: 100,
            value: 0,
        }
        const {min, max, step, name, value} = Object.assign(defaults, this.props);
        const showValue = this.props.showValue;
        const widthPercent = this.state.activeLineWidthPercent * 100;
        return(<div className="range-input-wrapper">
            {showValue && <span style={{left: widthPercent + '%', transform: `translateX(${-30 - (widthPercent * 0.4)}%)`}} className="range-input-value-text">{this.state.value}</span>}
            <input defaultValue={Number.isNaN(+value) ? min : +value} name={name} ref={this.inputRef} max={max} step={step} type="range" className="test-range-input" onInput={this.onInput} />
        </div>);
    }
}

export default Range;