import React from "react";
import BorderedButton from "../../atoms/bordered-button/BorderedButton";
import ExperienceItem from "../../atoms/experience-list-item/ExperienceItem";
import FileInput from "../../atoms/file-input/FileInput";
import HtmlDisplay from "../../atoms/html-display/HtmlDisplay";
import Line from "../../atoms/line/Line";
import ProgressBar from "../../atoms/progress-bar/ProgressBar";
import SelectInput from "../../atoms/select-input/SelectInput";
import TextInput from "../../atoms/text-input/TextInput";
import TitledInputWrapper from "../../atoms/titled-input/TitledInput";
import AboutModalContent from "../about-modal-content/AboutModalContent";
import ModalWrapper from "../modal-window/modal-window";
import PDFProps from "../PDFprops";
import ExperienceModalContent from '../experience-modal-content/ExperienceModalContent';
import SkillsList from "../skills-list/SkillsList";
import GroupWrapper from "../../atoms/group-wrapper/GroupWrapper";
import SkillSettings from "../../atoms/skill-settings/SkillSettings";
import countries from '../../../countries.json';

const generateId = function(): string {
    const date: Date = new Date();
    const randomNumber: number = Math.random();
    const randomString: string = date.getTime() + '_' + randomNumber;
    return randomString;
}
const skills: string[] = [
    'UI design',
    'UI design',
    'Prototyping',
    'UI',
    'UX',
    'Illustration'
];
interface UserInfoProps {
    userData: PDFProps;
    setUserData: (userData: PDFProps) => void;
}
interface UserInfoState {
    modalOpened: boolean;
    modalMode: string;
    modalId: string | null;
}
interface Skill {
    id:                string,
    skillName:         string,
    yearsOfExperience: number | undefined,
    level:             number,
    place: number
}
export default class UserInfo extends React.Component<UserInfoProps, UserInfoState> {
    userData: PDFProps;
    constructor(props: UserInfoProps) {
        super(props);
        this.userData = props.userData;
        this.state = {modalOpened: false, modalMode: 'about', modalId: null};
        this.addLink = this.addLink.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.setAbout = this.setAbout.bind(this);
        this.onInput = this.onInput.bind(this);
        this.onChnage = this.onChnage.bind(this);
        this.setValue = this.setValue.bind(this);
        this.getExperience = this.getExperience.bind(this);
        this.setExperience = this.setExperience.bind(this);
        this.editExperience = this.editExperience.bind(this);
        this.removeExperience = this.removeExperience.bind(this);
        this.changeSkill = this.changeSkill.bind(this);
        this.removeSkill = this.removeSkill.bind(this);
        this.moveSkillTop = this.moveSkillTop.bind(this);
        this.collectUserImage = this.collectUserImage.bind(this);
    }
    setUserData(userData: PDFProps) {
        this.props.setUserData(userData);
    }
    addLink() {
        this.userData.links.customLinks.push('');
        this.setUserData(this.userData);
    }
    closeModal() {
        this.setState({modalOpened: false});
    }
    openModal(modalMode: string, modalId: string | null) {
        this.setState({modalOpened: true, modalMode, modalId});
    }
    setAbout(value: string) {
        this.userData.about = value;
        this.setUserData(this.userData);
    }
    onInput(event: any) {
        const target = event.target as HTMLSelectElement;
        const {name, value} = target;
        if (name === 'unused') return;
        this.setValue(name, value);
    }
    onChnage(event: any) {
        const target = event.target as HTMLSelectElement;
        const {name, value} = target;
        if (name.includes('/') || name === 'unused') return;
        this.setValue(name, value);
    }
    setValue(name: string, value: string) {
        const dataPath = name;
        const pathElements: string[] = dataPath.split('/');
        const dataRecord: any = this.userData;
        console.log(pathElements);
        if (pathElements.length === 1) {
            dataRecord[pathElements[0]] = value;
        }
        else if (pathElements.length === 2) {
            // const cusomLinkElements = pathElements[1].split('_');
            const [fieldName, index] = pathElements[1].split('_');
            if (index) {
                dataRecord[pathElements[0]][fieldName][+index] = value;
            }
            else {
                dataRecord[pathElements[0]][fieldName] = value;
            }
        }
        this.userData = dataRecord;
        this.setUserData(this.userData);
    }
    getExperience() {
        const id = this.state.modalId;
        const experiences = this.props.userData.experience;
        if (id) return experiences[id];
    }
    setExperience(experienceData: UserExperienceData) {
        const id: string | undefined = experienceData.id;
        if (id)
        this.userData.experience[id] = experienceData;
        // console.log('Set experience');
        this.setUserData(this.userData);
    }
    editExperience(id: string | undefined) {
        if (id)
        this.openModal('experience', id);
    }
    removeExperience(id: string | undefined) {
        if (id) {
            delete this.userData.experience[id];
            this.setUserData(this.userData);
        }
    }
    setSkill(skillName: string) {
        const id = generateId();
        

        this.userData.skills = this.sortSkills(this.userData.skills);

        this.userData.skills[id] = {
            id, skillName,
            yearsOfExperience: undefined,
            level: 1,
            place: 0
        };
        this.setUserData(this.userData);
    }
    changeSkill(id: string, options: {yearsOfExperience?: number | undefined, level?: number}) {
        const fullSkillData = this.userData.skills[id];
        const newSkillData = Object.assign(fullSkillData, options);
        this.userData.skills[id] = newSkillData;
        this.setUserData(this.userData);
    }
    removeSkill(id: string) {
        delete this.userData.skills[id];
        this.setUserData(this.userData);
    }
    moveSkillTop(id: string) {
        this.userData.skills[id].place-=3;
        console.table(this.userData.skills);
        this.userData.skills = this.sortSkills(this.userData.skills);
        this.setUserData(this.userData);
    }
    sortSkills(skills: 
        {
            [key: string]: Skill}
        ) {
        let place = 0;
        return Object
        .fromEntries(Object
        .entries(skills)
        .sort((a, b) => a[1].place - b[1].place)
        .map((entry, i) => {
            place += 2;
            return [
                    entry[0],
                    {
                        ...entry[1],
                        place
                    }
            ]
        }
        ));
    }
    collectUserImage(file: File) {
        const reader = new FileReader;
        reader.readAsDataURL(file);
        reader.onloadend = () => {
            if (reader.result) {
                this.userData.img = reader.result;
                this.setUserData(this.userData);
            }
        }
    }
    render() {
        // console.log(this.userData);
        
        let writenFields: number = 0;
        const entries = Object.entries(this.userData);
        console.log(entries);
        entries.forEach((field, i) => {
            if (field[1] && field[1].length > 0 || typeof field[1] === 'object' && Object.entries(field[1]).find(entry => entry[1] && !!entry[1])) {
                
                writenFields++;
            }
            
        });
        const progress: number = Math.floor(writenFields / entries.length * 100);
        
        const {modalOpened, modalMode, modalId} = this.state;
        const userData = this.userData;
        let modalContent = undefined;
        if (modalMode === 'about') {
            modalContent = <AboutModalContent content={userData.about} setAbout={this.setAbout} />;
        }
        else if (modalMode === 'experience') {
            modalContent = <ExperienceModalContent modalId={modalId} setExperience={this.setExperience} userExperience={this.getExperience()} />
        }
        console.log(userData);
        
        return (
        <>
                {modalOpened && (<ModalWrapper close={this.closeModal}>
                    {modalContent}
                </ModalWrapper>)}
            <section onInput={this.onInput} onChange={this.onChnage} className="user-info-section">
                <h1 className="section-title bold">My resume</h1>
                <p className="progress-text font-mediusm"><span className="accent">{progress}%</span> Profile complitness</p>
                <ProgressBar className="user-info-progress" progress={progress} />
                <h2 className="info-block-header bold personal-info-header">personal information</h2>
                <div className="inputs-container">

                    <Line className="user-info-sky-line" />
                    <TitledInputWrapper title="First Name Last Name" protectedSign={false}>
                        <TextInput value={userData.name} placeholder="First Name Last Name" name="name" />
                    </TitledInputWrapper>
                    <FileInput name="unused" onFile={this.collectUserImage} />
                    <div className="contact-input-container">
                        <div className="inputs-group">
                            <TitledInputWrapper className="contact-input-wrapper" title="Phone" protectedSign={true}>
                                <TextInput value={userData.phone} placeholder="Phone" name="phone" />
                            </TitledInputWrapper>
                            <TitledInputWrapper className="contact-input-wrapper" title="Skype" protectedSign={true}>
                                <TextInput value={userData.skype} placeholder="Skype" name="skype"/>
                            </TitledInputWrapper>
                            <TitledInputWrapper className="contact-input-wrapper" title="Country" protectedSign={false}>
                                <SelectInput defaultValue={countries[0].name} name="country" values={countries.sort((a, b) => a.name.localeCompare(b.name)).map(country => ({title: country.name, value: country.name}))} />
                            </TitledInputWrapper>
                        </div>
                        <div className="inputs-group">
                            <TitledInputWrapper className="contact-input-wrapper" title="Email" protectedSign={false}>
                                <TextInput value={userData.email} placeholder="Email" name="email" />
                            </TitledInputWrapper>
                            <TitledInputWrapper className="contact-input-wrapper" title="Telegram" protectedSign={false}>
                                <TextInput value={userData.telegram} placeholder="Telegram" name="telegram" />
                            </TitledInputWrapper>
                            <TitledInputWrapper className="contact-input-wrapper" title="City" protectedSign={false}>
                                <TextInput value={userData.city} placeholder="City" name="city" />
                            </TitledInputWrapper>
                        </div>
                    </div>
                    <TitledInputWrapper className="link-input-wrapper" title="Profile LinkedIn" protectedSign={false}>
                        <TextInput value={userData.links.linkedInLink} placeholder="Profile LinkedIn" name="links/linkedInLink" />
                    </TitledInputWrapper>
                    <TitledInputWrapper className="link-input-wrapper" title="GitHub" protectedSign={false}>
                        <TextInput value={userData.links.gitHubLink} placeholder="GitHub" name="links/gitHubLink" />
                    </TitledInputWrapper>
                    <TitledInputWrapper className="link-input-wrapper" title="Portfolio" protectedSign={false}>
                        <TextInput value={userData.links.portfolioLink} placeholder="Portfolio" name="links/portfolioLink" />
                    </TitledInputWrapper>
                    {userData.links.customLinks.map((link: string, i) => (
                        <TitledInputWrapper key={'link_' + i} className="link-input-wrapper" title="Custom link" protectedSign={false}>
                            <TextInput value={link} placeholder="Custom link" name={"links/customLinks_" + i} />
                        </TitledInputWrapper>
                    ))}
                    <BorderedButton onClick={this.addLink} className="add-link-btn">+ add custom link</BorderedButton>
                </div>
                <h2 style={{marginTop: '35px'}} className="info-block-header bold">about me</h2>
                <div className="about-info-container" onClick={() => this.openModal('about', null)}>
                    {userData.about.length > 0 ? <HtmlDisplay>{this.userData.about}</HtmlDisplay> : (<BorderedButton>+ write</BorderedButton>)}
                </div>
                <Line className="user-info-sky-line" />
                <h2 onClick={() => this.openModal('experience', generateId())} style={{marginTop: '30px'}} className="info-block-header bold add-button">Experience</h2>
                <Line className="user-info-sky-line" />
                <ul className="experience-list">
                    {Object.values(this.props.userData.experience).map(experienceInfo => 
                    <li className="user-experience-list-item" key={experienceInfo.id}>
                        <ExperienceItem remove={this.removeExperience} edit={this.editExperience} experienceData={experienceInfo} />
                    </li>)}
                </ul>
                <h2 style={{marginTop: '20px'}} className="info-block-header bold">Skills</h2>
                <div className="skills-list-wrapper" onClick={(event: React.SyntheticEvent) => {
                    const target = event.target as HTMLDivElement;
                    if (target.classList.contains('skills-list-item') && target.textContent) {
                        this.setSkill(target.textContent);
                    }
                }}>
                    <GroupWrapper>
                        <SkillsList className="user-skills-list" skills={skills} />
                    </GroupWrapper>
                </div>
                <ul className="skills-settings-list">
                    {Object.values(userData.skills).sort((a, b) => a.place - b.place).map(skill => 
                        <li key={skill.id} style={{order: skill.place}}>
                            <SkillSettings {...skill} change={this.changeSkill} remove={this.removeSkill} moveTop={this.moveSkillTop}/>
                        </li>)}
                </ul>
            </section>
        </>
        )
    }
}
interface UserExperienceData {
    role:        string | undefined,
    companyName: string | undefined,
    startMonth:  string | undefined,
    startYear:   string | undefined,
    endMonth:    string | undefined,
    endYear:     string | undefined,
    description: string | undefined,
    id?:         string | undefined
}