import CommonProps from '../../common-props';
import classNames from 'classnames';
import './text-input.css';
import React from 'react';
interface ElemProps extends CommonProps {
    value?: string | undefined, placeholder: string, name?: string; onInput?: (event: React.SyntheticEvent) => void;
};

export default function TextInput(props: ElemProps) {
    const {className, style, placeholder, name, value, onInput} = props;
    return (<input onInput={onInput} defaultValue={value} placeholder={placeholder} type="text" className={classNames(className, 'text-input medium-font')} name={name} style={{...style}}></input>)
}

