import React from "react";
import CommonProps from '../../common-props';
import './modal-window.css';


interface ModalWrapperProps extends CommonProps {
    close: () => void;
    setAbout?: (value: string) => void;
}

export default class ModalWrapper extends React.Component <ModalWrapperProps> {
    constructor(props: ModalWrapperProps) {
        super(props);
    }
    render() {
        const {children} = this.props;
        return (<div onClick={(event: React.MouseEvent) => {(event.target as HTMLDivElement).classList.contains('modal-bg') && this.props.close()}} className="modal-bg">
            <div className="modal">
                {React.cloneElement(children as React.ReactElement<any>, {closeModal: this.props.close})}
            </div>
        </div>)
    }
}